  overflow-y: scroll;
  transform: translateX(100%);
}

.header__mobile-menu.is-open {
  visibility: visible;
  transform: translateX(0);
}

@media screen and (min-width: 1280px) {
  .header__mobile-menu {
    display: none;
  }
}

.header__mobile-menu .btn {
